export default {
    name: "prize",
    components: {},
    data() {
        return {
            list: null,
            page: 1,
            args: false,
            load: false,
        }
    },
    onShow: function () {
        const self = this;
        self.$showLoading({title: '加载中'});
        self.$request({
            url: self.$api.pond.prize,
            data: {
                page: 1,
            }
        }).then(info => {
            self.$hideLoading();
            if (info.code === 0) {
                self.list = info.data.list;
            }
        }).catch(e => {
            self.$hideLoading();
        });
    },

    /**
     * 页面上拉触底事件的处理函数
     */
    onReachBottom: function () {
        const self = this;
        if (self.args || self.load)
            return;
        self.load = true;
        let page = self.page + 1;
        self.$request({
            url: self.$api.pond.prize,
            data: {
                page: page,
            }
        }).then(info => {
            if (info.code === 0) {
                [self.page, self.args, self.list] = [page, info.data.list.length === 0, self.list.concat(info.data.list)];
            }
            self.load = false;
        });
    },
    methods: {
        submit: function (list) {
            const mchList = [{
                "mch_id": 0,
                "pond_id": list.id,
                "goods_list": [{
                    "id": list.goods.id,
                    "attr": list.attr_list,
                    "num": 1,
                    "cart_id": 0,
                    "goods_attr_id": list.attr_id
                }]
            }];

            uni.navigateTo({
                url: `/pages/order-submit/order-submit?mch_list=` +
                    JSON.stringify(mchList) +
                    `&preview_url=` + encodeURIComponent(this.$api.pond.order_preview) +
                    `&submit_url=` + encodeURIComponent(this.$api.pond.order_submit)
            });
        },
    }
}
